.card {
  background: #212833;

  &__inner {
    padding: 60px 0;
    background-color: var(--light-color);
    border-bottom-left-radius: 20%;
  }

  &-img {
    display: block;
    margin-bottom: 10px;
  }
  &__title {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 40px;
    line-height: 49.6px;
    color: #000000;
    text-transform: uppercase;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 3fr);
    list-style-type: none;
    padding-bottom: 30px;
    gap: 30px 20px;
  }

  &__item {
    max-width: 427px;
    height: 614px;
    border: 1px solid #dddddd;
    border-radius: 20px;
  }

  &__info {
    margin-bottom: 20px;
  }

  &__info-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 24.8px;
    padding-bottom: 10px;
  }

  &__info-text {
    font-weight: 500;
    font-size: 14px;
    line-height: 17.36px;
    color: #969696;

    span {
      color: #000000;
    }
  }

  &__info-price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  &__price-text {
    max-width: 208px;
    height: 34px;
    line-height: 17.36px;
    font-size: 14px;
    font-weight: 300;
    text-align: left;
  }

  &__info-key {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
  }

  &__price-request {
    display: flex;
    flex-direction: column-reverse;
  }

  &__price-btn {
    max-width: 397px;
    height: 49px;
    display: inline-block;
    padding: 14px 100px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20.8px;
    text-align: center;
    color: #e42424;
    border-radius: 10px;
    border: 1px solid #e42424;
  }
}

.price {
  font-weight: 700;
  font-size: 18px;
  line-height: 22.32px;
  text-align: right;
  color: #000000;
}

.key {
  width: 149px;
  height: 32px;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  text-align: left;
  color: #969696;
}

.key-2 {
  width: 186px;
  height: 39px;
  font-weight: 300;
  font-size: 14px;
  line-height: 17.36px;
  text-align: right;
  color: #969696;
}

.card__padding {
  padding: 0 15px;
}

.card-btn {
  text-align: center;
  width: 427px;
  height: 68px;
  display: block;
  margin: 0 auto;
}

.card-width {
  max-width: 427px;
}

.content {
  padding-bottom: 30px;
  max-width: 1340px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 50px;
  align-items: flex-start;
}

.swiper {
  width: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  gap: 20px;
}

.swiper-slide {
  grid-column: 3 span;
  min-width: 300px;
}

.content-card {
  width: 427px;
  min-height: 200px;
}

@media (max-width: 1500px) {
  .slider-2 .swiper-wrapper {
    display: flex;
    column-gap: 0;
  }
}

@media (max-width: 1280px) {
  .slider-1 .swiper-wrapper {
    display: flex;
    column-gap: 0;
  }

  .content-card {
    width: 400px;
    min-height: 200px;
  }

  .card__price-btn {
    padding: 15px 15px;
  }
}

@media (max-width: 460px) {
  .card-btn {
    padding: 17px 50px;
    width: 300px;
    height: 55px;
    font-size: 16px;
    line-height: 20.8px;
  }

  .card__item{
    padding: 0 10px;
    width: 320px;
    height: 570px;
  }

  .card__price-btn {
    width: 280px;

  }

  .content-card {
    max-width: 400px;
    min-height: 200px;
  }
}
