.hero {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__container {
    padding-top: 150px;
    padding-bottom: 100px;
  }

  &__title {
    max-width: 1340px;
    height: 80px;
    font-weight: 900;
    font-size: 76px;
    line-height: 105.68px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: transparent;
    -webkit-text-stroke: 1px #dadada;
    text-stroke: 1px #dadada;
    -webkit-background-clip: text;
    background-clip: text;

    span {
      display: inline-block;
      height: 85px;
      -webkit-text-stroke: 0px;
      text-stroke: 0px;
      -webkit-background-clip: text;
      background-image: linear-gradient(#464646, #fff);
    }
  }

  &__inner {
    padding-top: 20px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__text-left {
    max-width: 635px;
    max-height: 26px;
    font-weight: 300;
    font-size: 20px;
    line-height: 26px;
    color: var(--light-color);
    opacity: 0.8;
  }

  &__inner-container {
    max-width: 447px;
    max-height: 130px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__text-right {
    font-weight: 300;
    font-size: 20px;
    line-height: 26px;
    text-align: right;
    color: var(--light-color);
    opacity: 0.8;

    span {
      display: inline-block;
      font-weight: 700;
      font-size: 20px;
      line-height: 26px;
      text-align: right;
    }
  }

  &__img-container {
    margin: 0 auto;
  }

  &__img-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 430px;
    background-image: linear-gradient(to top, black -15%, transparent 90%);
  }

  &__btn {
    margin: 0 auto;
    display: inline-block;
    margin-bottom: 40px;
  }

  &__img-text {
    padding-bottom: 71px;
    max-width: 1334px;
    height: 46px;
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    color: var(--light-color);
    opacity: 0.8;

    span {
      display: inline-block;
    }
  }
}

.bottom-text {
  align-self: flex-end;
  max-width: 302px;
  height: 49px;
}

@media (max-width: 1369px) {
  .hero {
    background-size: 1370px 100%;
  }

  .hero__title {
    font-size: 50px;
    line-height: 62px;
    text-align: center;

    span {
      font-size: 26px;
      height: 62px;
    }
  }

  .hero__text-left {
    padding-bottom: 10px;
  }

  .hero__text-right {
    text-align: center;
  }

  .hero__inner {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }

  .bottom-text {
    align-self: center;
  }

  .hero__btn {
    padding: 17px 77px;
  }
}

@media (max-width: 720px) {
  .hero-container {
    padding-top: 125px;
  }
}

@media (max-width: 613px) {
  .hero {
    background-size: 900px 100%;
  }

  .hero__img-text {
    width: 320px;
    height: 56px;
    font-weight: 300;
    font-size: 12px;
    line-height: 13.8px;
    text-align: center;
  }

  .hero__text-left {
    padding-top: 15px;
    padding-bottom: 30px;
    font-weight: 300;
    font-size: 14px;
    line-height: 18.2px;
    text-align: center;
  }

  .hero__text-right {
    font-weight: 300;
    font-size: 12px;
    line-height: 15.6px;
    text-align: center;

    span {
      font-weight: 700;
      font-size: 12px;
      line-height: 15.6px;
      text-align: center;
    }
  }

  .hero__inner-container {
    padding-top: 12px;
  }
}

@media (max-width: 400px) {
  .hero {
    background-size: 500px 100%;
  }

  .hero__btn {
    font-weight: 400;
    font-size: 16px;
    line-height: 20.8px;
  }
}
