.about {
  background-color: #212833;
  padding-bottom: 150px;

  &__inner {
    position: relative;
  }

  &__title {
    padding-top: 50px;
    font-weight: 700;
    font-size: 140px;
    line-height: 173.6px;
    text-align: center;
    text-transform: uppercase;
    color: transparent;
    -webkit-text-stroke: 1px #676767;
    text-stroke: 1px #676767;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 30px;
    position: relative;
    bottom: 90px;
  }

  &__left-title {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 40px;
    line-height: 49.6px;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &__list {
    display: flex;
    align-items: center;
    flex-direction: column;
    list-style-type: none;
    gap: 25px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 35px;
  }

  &__numbers {
    width: 91px;
    font-weight: 700;
    font-size: 52px;
    line-height: 64px;
    text-align: center;
    text-transform: uppercase;
    color: transparent;
    -webkit-text-stroke: 1px #676767;
    text-stroke: 1px #676767;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__text {
    max-width: 606px;
    height: 60px;
    font-weight: 400;
    font-size: 24px;
    line-height: 29.76px;
    color: var(--light-color);
  }
}

@media (max-width: 1670px) {
  .about__left {
    padding: 0 30px;
  }
}

@media (max-width: 1369px) {
  .about__container {
    padding-top: 60px;
    position: static;
    flex-wrap: wrap;
    gap: 50px;
  }

  .about__title {
    padding: 0;
    display: none;
  }

  .about__img {
    padding-left: 30px;
  }
}

@media (max-width: 800px) {
  .about__text {
    max-width: 606px;
    font-size: 20px;
    line-height: 22px;
  }

  .about__numbers {
    font-size: 40px;
    line-height: 44.8px;
  }
}

@media (max-width: 613px) {
  .about{
    padding-bottom: 100px;
  }

  .about__left-title {
    font-size: 18px;
    line-height: 22.32px;
  }

  .about__text {
    font-size: 12px;
    line-height: 14.88px;
  }

  .about__numbers {
    font-size: 40px;
    line-height: 44.8px;
  }
}
