.advantage {
  padding-top: 100px;
  background-color: #212833;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__item {
    max-width: 566px;
    height: 100%;
    padding: 18px 120px 18px 58px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24.8px;
    text-align: left;
    background-color: var(--light-color);
    border-radius: 20px;
    list-style-type: none;
  }
}

.advantage-padding{
  padding: 28px 120px 27px 58px;
}

@media (max-width: 1369px){
  .advantage__inner{
    flex-wrap: wrap;
  }
}
