.site__container {
  position: relative;
}

.header {
  padding-top: 40px;
  position: absolute;
  width: 100%;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 158px;
  }

  &__logo {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 26px;
    line-height: 32.24px;
    color: var(--light-color);
  }

  &__link {
    padding: 12px 10px 10px;
  }

  &__nav-list {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
  }
}

.burger {
  @include burger;
}

.nav__link {
  font-family: var(--second-family);
  font-size: 20px;
  font-weight: 400;
  line-height: 24.2px;
  color: var(--light-color);
  cursor: pointer;
}

.burger {
  position: relative;
  width: 50px;
  height: 30px;
  display: none;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.burger__line {
  position: absolute;
  left: 10px;
  top: 50%;
  width: 60%;
  height: 1px;
  background-color: var(--light-color);
  transform: translateY(-50%);
  transition: opacity 0.3s ease-in-out;
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 10px;
  width: 60%;
  height: 1px;
  background-color: var(--light-color);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out,
    bottom 0.3s ease-in-out;
}

.burger::before {
  top: 7px;
}

.burger::after {
  top: 20px;
}

.burger--active .burger__line {
  opacity: 0;
}

.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
}

.burger--active::after {
  bottom: auto;
  top: 50%;
  transform: rotate(-45deg);
}

.stop-scroll {
  overflow: hidden;
}

.header__item-none {
  display: none;
}

@media (max-width: 1024px) {
  .header__item-none {
    display: block;
  }

  .header__link {
    display: none;
  }

  .burger {
    display: block;
  }

  .nav {
    position: fixed;
    right: 0;
    top: 100px;
    z-index: 1000;
    background-color: #000;
    opacity: 0.8;
    padding: 30px;
    transform: translateX(100%);
    transition: transform 0.6s ease-in-out;
    width: 100%;
  }

  .nav__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }

  .menu--active {
    transform: none;
  }
}

@media (max-width: 720px) {
  .header__logo {
    font-size: 20px;
    line-height: 24.8px;
  }
}
