.team {
  background-color: #212833;
  display: none;

  &__wrapper {
    max-height: 1500px;
    padding: 100px 0;
    background-color: var(--light-color);
    border-radius: 300px;
  }

  &__title {
    padding-bottom: 30px;
    font-weight: 700;
    font-size: 40px;
    line-height: 49.6px;
    color: #000;
  }

  &__inner-title {
    margin-bottom: 10px;
    display: inline-block;
    padding: 14px 40px;
    border-radius: 20px;
    background-color: #b40d0d;
    color: #fff;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  &__inner-container {
  }

  &__list {
    height: 330px;
    padding-bottom: 25px 0 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    border: 1px solid #d9d9d9;
    border-radius: 20px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 10px;
    list-style-type: none;
  }

  &__item-name {
    font-weight: 700;
    font-size: 16px;
    line-height: 19.84px;
    color: #000;
    text-align: center;
  }

  &__item-inner {
    display: flex;
    justify-content: space-between;
  }

  &__item-city {
    font-weight: 400;
    font-size: 16px;
    line-height: 19.84px;
  }

  &__item-tel {
    font-weight: 400;
    font-size: 10px;
    line-height: 17.36px;
    color: #B9B9B9;
  }

  &-btn{
    text-align: center;
    padding: 14px 50px;
    max-width: 205px;
    height: 45px;
  }

  &-img {
    display: block;
  }

  &__img-container {
    position: relative;
    bottom: 1400px;
    left: 300px;
  }


  &-flex{
    height: 100%;
    flex-wrap: wrap;
  }
}

.team-height{
  height: 330px;
}

.main-team {
  max-width: 483px;
}

