.footer{
  background-color: #212833;

  padding-top: 40px;
  position: absolute;
  width: 100%;

  &__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 158px;
  }

  &__logo {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 26px;
    line-height: 32.24px;
    color: var(--light-color);
  }

  &__link{
    padding: 12px 10px 10px;
  }

  &__nav-list{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
  }


  &__container{
    padding: 50px;
    background-color: #fff;
    border-top-left-radius: 100px;
    border-top-right-radius: 100px;
  }

  &__inner{
    gap: 139px;
  }

  &__logo, &__nav-link{
    color: #000;
  }
}


@media (max-width: 1000px) {
  .footer__container{
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
  }

  .footer__inner{
    flex-wrap: wrap;
    gap: 20px;
    align-items: center;
    justify-content: center;
  }

  .footer__nav-list {
    max-width: 250px;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .footer__nav-list{
    flex-wrap: wrap;
  }
}


