.order {
  background-color: #212833;
  padding-bottom: 100px;

  &__wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 20px;
  }

  &__left {
    max-width: 950px;
    height: 617px;
    padding: 30px 167px 30px 300px;
    background-color: #fff;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }

  &__title {
    padding-bottom: 20px;
    font-size: 36px;
    font-weight: 700;
    line-height: 44.64px;
    color: #000;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__label {
    border-radius: 10px;
    border: 1px solid #8e8e8e;
    position: relative;
  }

  &__input {
    text-align: center;
    width: 100%;
    height: 100%;
    padding: 15px 100px;
    border-radius: 10px;

    &::placeholder {
      text-align: center;
      color: #9d9d9d;
    }
  }

  &__btn {
    padding: 14px 158px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20.8px;
  }

  &__img {
    display: block;
  }
}

.just-validate-error-label {
  position: absolute;
  bottom: 47px;
  left: 10px;
  font-weight: 400;
  font-size: 12px;
  line-height: 16.34px;
}

@media (max-width: 800px) {
  .order__left {
    padding: 30px;
  }


}





@media (max-width: 500px) {
  .order__title {
    font-size: 18px;
    line-height: 22.32px;
  }


  .order__input{
    padding: 20px 133px;
  }
}
