.process {
  background-color: #212833;

  &__flex {
    display: flex;
    justify-content: flex-end;
  }

  &__wrapper {
    padding: 100px;
    max-width: 1207px;
    max-height: 953px;
    border-top-left-radius: 10%;
    border-bottom-left-radius: 10%;
    background-color: #fff;
    position: relative;
    top: 200px;
  }

  &__title {
    margin-bottom: 39px;
    max-width: 710px;
    height: 200px;

    font-size: 40px;
    font-weight: 700;
    line-height: 49.6px;
    text-transform: uppercase;
    color: #000;

    span {
      color: #b81e1e;
    }
  }

  &__list {
    padding-bottom: 30px;
    max-width: 734px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }

  &__numbers {
    width: 84px;
    height: 84px;
    padding: 19px 16px;
    background-color: #000;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item-numbers {
    width: 52px;
    height: 45px;
    font-weight: 700;
    font-size: 36px;
    line-height: 44.64px;
    -webkit-text-stroke: 1px var(--light-color);
    text-stroke: 1px var(--light-color);
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 29.76px;
    color: #000;
  }

  &__item-text {
    font-weight: 300;
    font-size: 16px;
    line-height: 19.84px;
    color: #000;
  }

  &__bottom{
    padding: 22px 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 734px;
    height: 123px;
    background-color: #000;
    border-radius: 20px;
  }

  &__bottom-text {
    font-weight: 500;
    font-size: 20px;
    line-height: 24.8px;
    color: var(--light-color);
  }

  &__bottom-time {
    font-family: var(--second-family);
    font-size: 20px;
    font-weight: 500;
    line-height: 24.2px;
    color: #6C6C6C;
  }

  &__img {
    display: block;
    width: 100%;
  }
}

.numbers-width {
  width: 64px;
}
