.social {
  padding-bottom: 100px;
  background-color: #212833;

  &__telegram {
    position: relative;
    bottom: 200px;
  }

  &__telegram-top {
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    background-color: var(--light-color);
    border-radius: 20px;
    position: relative;
    top: 200px;
  }

  &__telegram-title {
    padding-top: 50px;
    font-weight: 700;
    font-size: 36px;
    line-height: 44.64px;
    text-align: center;
    text-transform: uppercase;
    color: #000;
  }

  &__telegram-text {
    max-width: 1005px;
    height: 50px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24.8px;
    color: #797979;
  }

  &__telegram-btn {
    padding: 21px 93px;
  }

  &__youtube-container {
    margin-bottom: 30px;
  }

  &__youtube-title {
    margin: 0px auto 30px;
    max-width: 989px;
    height: 100px;
    font-weight: 700;
    font-size: 40px;
    line-height: 49.6px;
    text-align: center;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &__youtube-btn{
    padding: 21px 98px;
  }
}



